"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import { AxiosError } from "axios";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Wallet, ArrowDownRight, ArrowUpRight, History, CreditCard, ShieldCheck } from "lucide-react";
import { toast } from "sonner";
import Link from "next/link";
import { cn } from "@/lib/utils";
import { motion, AnimatePresence } from "framer-motion";

interface Transaction {
    id: number;
    reference: string;
    type: string;
    amount: number;
    description?: string;
    status: string;
    created_at: string;
}

export default function WalletPage() {
    const [balance, setBalance] = useState(0);
    const [transactions, setTransactions] = useState<Transaction[]>([]);
    const [amount, setAmount] = useState("");
    const [loading, setLoading] = useState(false);
    const [pageLoading, setPageLoading] = useState(true);

    useEffect(() => {
        const fetchWallet = async () => {
            try {
                const res = await api.get<{ balance: number; transactions: { data: Transaction[] } }>('/wallet');
                setBalance(res.data.balance);
                setTransactions(res.data.transactions.data);
            } catch (e) {
                console.error(e);
            } finally {
                setPageLoading(false);
            }
        };
        fetchWallet();
    }, []);

    const handleTopUp = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);
        try {
            const res = await api.post('/wallet/topup', { amount });
            if (res.data.authorization_url) {
                toast.loading("Redirecting to payment gateway...");
                window.location.href = res.data.authorization_url;
            } else {
                toast.success("Top up initiated. Check your phone for prompt.");
            }
        } catch (e) {
            const error = e as AxiosError<{ message: string }>;
            toast.error(error.response?.data?.message || "Top up failed. Please try again.");
        } finally {
            setLoading(false);
        }
    };

    if (pageLoading) return <div className="animate-pulse p-10">Loading wallet information...</div>;

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
            className="space-y-6"
        >
            <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                <div>
                    <h2 className="text-3xl font-bold tracking-tight text-primary">My Wallet</h2>
                    <p className="text-muted-foreground font-medium">Manage your funds and view transaction history.</p>
                </div>
            </div>

            <div className="grid gap-6 md:grid-cols-2">
                <Card className="sneat-card border-none bg-primary text-primary-foreground overflow-hidden relative">
                    <div className="absolute right-[-20px] bottom-[-20px] opacity-10">
                        <Wallet className="w-48 h-48" />
                    </div>
                    <CardHeader>
                        <CardTitle className="text-sm font-medium uppercase tracking-wider opacity-80">Available Funds</CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        <div className="text-5xl font-bold">GHS {Number(balance).toFixed(2)}</div>
                        <p className="text-xs opacity-70 flex items-center gap-1">
                            <ShieldCheck className="w-3 h-3" />
                            Funds are secured and encrypted
                        </p>
                    </CardContent>
                </Card>

                <Card className="sneat-card">
                    <CardHeader>
                        <CardTitle className="text-lg">Add Funds</CardTitle>
                        <CardDescription>Enter an amount to top up via Paystack.</CardDescription>
                    </CardHeader>
                    <CardContent>
                        <form onSubmit={handleTopUp} className="space-y-4">
                            <div className="relative">
                                <span className="absolute left-4 top-1/2 -translate-y-1/2 font-bold text-gray-400">GHS</span>
                                <Input
                                    type="number"
                                    placeholder="0.00"
                                    value={amount}
                                    onChange={(e) => setAmount(e.target.value)}
                                    required
                                    min="1"
                                    className="h-14 pl-14 text-xl font-bold bg-gray-50 dark:bg-gray-800 border-gray-100 dark:border-gray-700 focus:bg-white dark:focus:bg-gray-900 transition-all"
                                />
                            </div>
                            <Button type="submit" disabled={loading} className="w-full h-12 shadow-lg shadow-primary/20">
                                <CreditCard className="w-4 h-4 mr-2" />
                                {loading ? "Initializing..." : "Proceed to Payment"}
                            </Button>
                        </form>
                    </CardContent>
                </Card>
            </div>

            <Card className="sneat-card">
                <CardHeader className="flex flex-row items-center justify-between border-b border-gray-100 dark:border-gray-800 bg-white dark:bg-gray-900 rounded-t-lg">
                    <CardTitle className="text-lg flex items-center gap-2">
                        <History className="w-5 h-5 text-primary" />
                        Recent Transactions
                    </CardTitle>
                    <Button asChild variant="ghost" size="sm" className="text-primary hover:bg-primary/5">
                        <Link href="/dashboard/transactions">View All History</Link>
                    </Button>
                </CardHeader>
                <CardContent className="p-0">
                    <div className="relative overflow-x-auto">
                        <table className="w-full text-sm text-left">
                            <thead className="text-[10px] text-muted-foreground uppercase bg-gray-50/50 dark:bg-gray-800/50 border-b border-gray-100 dark:border-gray-800">
                                <tr>
                                    <th className="px-6 py-4">Status</th>
                                    <th className="px-6 py-4">Reference</th>
                                    <th className="px-6 py-4">Description</th>
                                    <th className="px-6 py-4">Amount</th>
                                    <th className="px-6 py-4">Date</th>
                                </tr>
                            </thead>
                            <tbody className="divide-y divide-gray-100 dark:divide-gray-800">
                                {transactions.length === 0 ? (
                                    <tr>
                                        <td colSpan={5} className="px-6 py-20 text-center">
                                            <History className="w-12 h-12 text-muted-foreground/20 mx-auto mb-4" />
                                            <p className="text-muted-foreground">No transactions found.</p>
                                        </td>
                                    </tr>
                                ) : transactions.slice(0, 5).map((trx) => (
                                    <tr key={trx.id} className="hover:bg-accent/5 transition-colors">
                                        <td className="px-6 py-4">
                                            <div className={cn(
                                                "w-8 h-8 rounded-full flex items-center justify-center",
                                                trx.type === 'credit' ? "bg-green-100 text-green-600 dark:bg-green-900/30 dark:text-green-400" : "bg-red-100 text-red-600 dark:bg-red-900/30 dark:text-red-400"
                                            )}>
                                                {trx.type === 'credit' ? <ArrowDownRight className="w-4 h-4" /> : <ArrowUpRight className="w-4 h-4" />}
                                            </div>
                                        </td>
                                        <td className="px-6 py-4">
                                            <p className="font-mono text-xs font-bold text-gray-500 dark:text-gray-400">#{trx.reference}</p>
                                        </td>
                                        <td className="px-6 py-4">
                                            <p className="font-medium text-gray-900 dark:text-gray-200">{trx.description || (trx.type === 'credit' ? 'Wallet Topup' : 'Bundle Purchase')}</p>
                                            <p className="text-[10px] text-muted-foreground capitalize">{trx.status}</p>
                                        </td>
                                        <td className="px-6 py-4">
                                            <span className={cn(
                                                "font-bold text-lg",
                                                trx.type === 'credit' ? "text-green-600 dark:text-green-400" : "text-gray-900 dark:text-gray-100"
                                            )}>
                                                {trx.type === 'credit' ? '+' : '-'} GHS {trx.amount}
                                            </span>
                                        </td>
                                        <td className="px-6 py-4 text-muted-foreground">
                                            {new Date(trx.created_at).toLocaleDateString()}
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </CardContent>
            </Card>
        </motion.div>
    );
}
